import { mapActions } from 'vuex'

export default {
    data () {
      return {
        isShowRegister: true,
        isShowLogin: false,
        register: {
          username: '',
          password: '',
          notice: '创建账号后，请记住用户名和密码',
          isError: false
        },
        login: {
          username: '',
          password: '',
          notice: '输入用户名和密码',
          isError: false
        }
      }
    },
  
    methods: {

      ...mapActions({
        loginUser: 'login',
        registerUser: 'register'
      }),

      showRegister() {
        this.isShowRegister = true
        this.isShowLogin = false
      },
  
      showLogin() {
        this.isShowRegister = false
        this.isShowLogin = true
      },
  
      onRegister() {
        if(!/^[\w\u4e00-\u9fa5]{3,15}$/.test(this.register.username)) {
          this.register.isError = true
          this.register.notice = '用户名3~15个字符，仅限于字母数字下划线中文'
          return 
        }
        if(!/^.{6,16}$/.test(this.register.password)) {
          this.register.isError = true
          this.register.notice = '密码长度为6~16个字符'
          return
        }

        this.registerUser({ username: this.register.username, password: this.register.password })
        .then(() => {
          this.register.isError = false
          this.register.notice = ''
          this.$router.push({ path: '/notebooks' })
        }).catch(err => {
          this.register.isError = true
          this.register.notice = err.msg
        })
      },
  
      onLogin() {
        if(!/^[\w\u4e00-\u9fa5]{3,15}$/.test(this.login.username)) {
          this.login.isError = true
          this.login.notice = '用户名3~15个字符，仅限于字母数字下划线中文'
          return 
        }
        if(!/^.{6,16}$/.test(this.login.password)) {
          this.login.isError = true
          this.login.notice = '密码长度为6~16个字符'
          return
        }
        

        this.loginUser({ username: this.login.username, password: this.login.password })
        .then(() => {
          this.login.isError = false
          this.login.notice = ''
          this.$router.push({ path: '/notebooks' })
        }).catch(err => {
          this.login.isError = true
          this.login.notice = err.msg
        })
      },
  
      validRegisterUsername() {
        if(!/^[\w\u4e00-\u9fa5]{3,15}$/.test(this.register.username)) {
          this.register.isError = true
          this.register.notice = '用户名3~15个字符，仅限于字母数字下划线中文'
          return 
        }
        this.register.isError = false
        this.register.notice = ''
      },
  
      validRegisterPassword() {
        if(!/^.{6,16}$/.test(this.register.password)) {
          this.register.isError = true
          this.register.notice = '密码长度为6~16个字符'
          return
        }
        this.register.isError = false
        this.register.notice = ''
      },
      
      validLoginUsername() {
        if(!/^[\w\u4e00-\u9fa5]{3,15}$/.test(this.login.username)) {
          this.login.isError = true
          this.login.notice = '用户名3~15个字符，仅限于字母数字下划线中文'
          return 
        }
        this.login.isError = false
        this.login.notice = ''
      },
  
      validLoginPassword() {
        if(!/^.{6,16}$/.test(this.login.password)) {
          this.login.isError = true
          this.login.notice = '密码长度为6~16个字符'
          return
        }
        this.login.isError = false
        this.login.notice = ''
      }
    }
  }


